.title-decor * {
  max-width: max-content;
  position: relative;
  z-index: 2;
  background-color: #F5F3F0;
  display: inline-block;
  padding-right: 10px;
}
.title-decor:before, .title-decor:after {
  content: "";
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  background-color: #2B2A29;
  z-index: 1;
}
.title-decor:before {
  height: 2px;
  top: calc(50% - 6px);
}
.title-decor:after {
  height: 1px;
  bottom: calc(50% - 5.5px);
}

.bg-dark .title-decor * {
  background-color: #2B2A29;
}
.bg-dark .title-decor:before, .bg-dark .title-decor:after {
  background-color: #DADADA;
