.title-decor__reverse:before {
  height: 1px;
}
.title-decor__reverse:after {
  height: 2px;
}

.title-decor__under * {
  background-color: transparent;
  padding: 0;
}
.title-decor__under:before {
  height: 2px;
  top: unset;
  bottom: -10px;
}
.title-decor__under:after {
  height: 1px;
  bottom: 0;
}

